        drawingMarker(cataindex, map, position, name, my_address, my_link, phone) {

            // var position = new kakao.maps.LatLng(my_point[1], my_point[0]);
            // console.log( cataindex, my_address, my_link);
            var imageSrc = '';
            if( cataindex ==10) {
                imageSrc = '/img/jeju_cafe.png';
            } else if (cataindex ==11) {
                imageSrc = '/img/jeju_rest.png';
            } else if (cataindex ==12) {
                imageSrc = '/img/seogwipo_cafe.png';
            } else if (cataindex ==13) {
                imageSrc = '/img/seogwipo_rest.png';
            } else if (cataindex ==14) {
                imageSrc = '/img/seogwipo_cafe.png';
            }

            var imageSize = new kakao.maps.Size(16, 17);
            var imageOption = {offset: new kakao.maps.Point(8, 9)};            
            var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption)
            var marker = new kakao.maps.Marker({
                position : position,
                clickable: true,
                image : markerImage,
                map : map,
                title : name,
            });


            // var content =  '<div style="width:250px; height: 150px; padding: 5px;">';
            //     content += '    <a href='+my_link+' target="_blank"><strong> [  : '+name+']</strong></a>';
            //     content += '    <div>  : '+my_address+' </div>';
            //     content += '</div>';
            
            // var content = '<div style="width:250px; height: 100px; padding: 5px;"> <div></div>' +
            var content = '<div class="placeinfo">'+
                '   <a class="title" href="' + my_link + '" target="_blank" title="' + name + '">' + name + '</a>';
                content += '<div> <span title="' + my_address + '">' + my_address + '</span> </div>';   
                content += '      <span class="tel"> Tel. ' + phone + '</span>'; 
                content += '<div class="after"></div>';

            // console.log(content);

            var iwRemveable = true;
            var infowindow = new kakao.maps.InfoWindow({
                content: content,
                removable: iwRemveable,
            });            
            new kakao.maps.event.addListener(marker, 'click', function() {                                  
                infowindow.open(map, marker);
            })

            if( cataindex ==10) {
                jejucafeMarkers.push(marker);
            } else if (cataindex ==11) {
                jejurestMarkers.push(marker);
            } else if (cataindex ==12) {
                seogwipocafeMarkers.push(marker);
            } else if (cataindex ==13) {
                seogwiporestMarkers.push(marker);
            } else if (cataindex ==14) {
                hotelMarkers.push(marker);
            }
        },

        

        getdataFromkakao() {
            
            var container = document.getElementById("map");            
            var options = {
                center: new kakao.maps.LatLng(33.40, 126.58),
                level: 8,
            };
            this.map = new kakao.maps.Map(container, options);
            var mmap = toRaw(this.map);
            
            new kakao.maps.event.addListener(mmap, 'idle', searchPlaces);            
            var ps = new kakao.maps.services.Places(mmap);

            function searchPlaces() {                
                ps.categorySearch('AD5', placesSearchCB, {useMapBounds:true}); 
            }
            
            function placesSearchCB (data, status, pagination) {
                if (status === kakao.maps.services.Status.OK) {
                    for (var i=0; i<data.length; i++) {
                        displayMarker(data[i]);
                        if(!(searchData.some(v => v.id === data[i].id))) {                            
                            searchData.push(data[i]);
                        }                       
                    }
                    console.log(searchData.length);       
                }
            }
            
            function displayMarker(place) {
                var marker = new kakao.maps.Marker({
                    map: mmap,
                    position: new kakao.maps.LatLng(place.y, place.x) 
                });

                kakao.maps.event.addListener(marker, 'click', function() {
                    infowindow.setContent('<div style="padding:5px;font-size:12px;">' + place.place_name + '</div>');
                    infowindow.open(mmap, marker);
                });
            }
        },
        
        kakaodatamyfileWrite() {

            let header ='{\n';
                header += '"type": "FeatureCollection",\n';
                header += '"name": "jeju_rest_all",\n';
                header += '"crs": { "type": "name", "properties": { "name": "urn:ogc:def:crs:OGC:1.3:CRS84" } },\n';
                header += '"features":\n';

            const data = header+JSON.stringify(searchData)+'\n}';
            const blob = new Blob([data], { type: 'text/plain' });
            const url = URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', 'jeju_rest_alll.txt');
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);            
        },        
        
    },

